html {
  margin:0 auto;
  padding:0;
}
body {
  margin:0 auto;
  padding:20px;
}
.App {
  display:grid;
  grid-template-columns:1fr 1fr 1fr;
  row-gap:25px;
  column-gap:10px;
  margin-left:250px;
}
.sidebanner {
  height:100%;
  width:250px;
  position:fixed;
  z-index:-1;
  top:0;
  left:0;
  background-color:rgba(102, 103, 171, 1);
  overflow-x:hidden;
}
.card-title, .card-text {
  color:#444444;
}
.card-footer {
  font-size:0.9em;
}
.openbtn, .closebtn {
  font-size:20px;
  cursor:pointer;
  background-color:#fff;
  color:white;
  border:none;
  margin-left:-20px;
}
.closebtn {
  background-color:transparent;
}
@media only screen and (max-width: 1024px) {
  .App {
    display:grid;
    grid-template-columns:1fr 1fr;
    row-gap:25px;
    margin-left:0;
  }
  .sidebanner {
    display:none;
  }
  .bi-play-circle, .bi-dice-1 {
    display:none;
  }
  .openbtn {
    color:gray;
  }
}
@media only screen and (max-width: 480px) {
  .App {
    display:grid;
    grid-template-columns:1fr;
    row-gap:25px;
    margin-left:0;
  }
  .sidebanner {
    display:none;
  }
  .bi-play-circle, .bi-dice-1 {
    display:none;
  }
  .openbtn {
    color:gray;
  }
}
a {
  text-decoration:none !important;
  color:black !important;
}
.card-text {
  padding-left:10px;
}
.loading-gif {
  display:block;
  margin:0 auto;
}
.sidebar {
  height:100%;
  width:0;
  position:fixed;
  z-index:1;
  top:0;
  left:0;
  background-color:rgba(102, 103, 171, 0.9);
  overflow-x:hidden;
  padding-top:80px;
  transition:0.1s;
}
.sidebar .closebtn {
  position:absolute;
  top:0;
  right:25px;
  font-size:36px;
  margin-left:50px;
}
#main {
  transition:margin-left .1s;
  padding: 20px;
}
@media screen and (max-height: 450px) {
  .sidebar {padding-top: 15px;}
  .sidebar a {font-size: 18px;}
}
.carousel-indicators {display:none !important;}
.carousel-control-prev {display:none !important;}
.tippy-tooltip {
   font-size: 0.5rem !important;
}
